.footer {
  min-height: 273px;
  background: $dark-gray;
  color: $white;

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 60px;
}


.footer__logo {
  margin-bottom: 53px;
}

.footer__socials {

  ul {
    display: flex;
  }

  li:not(:last-of-type) {
    margin-right: 23px;
  }
}

.footer__info {
  display: flex;
  justify-content: flex-end;
}

.footer__item {
  width: 17%;
  margin-left: 22px;

  &:first-of-type {
    margin-left: 0;
  }

  &:last-of-type {
    // width: 17%;
    margin-left: 97px;
  }

  h2 {
    margin: 22px 0 15px 0;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 0.12em;
  }

  p {
    margin: 0 0 12px 0;
    font-size: 10px;
    line-height: 12px;
    // letter-spacing: 0.1em;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  a {
    color: $white;
    font-size: 10px;
    letter-spacing: 0.13em;

    &[href^="tel"] {
      display: block;
      margin-bottom: 10px;
    }
  }

  li {
    line-height: 50%;
  }

  li:not(:last-of-type) {
    margin-bottom: 10px;
  }

  address {
    font-style: normal;
    font-size: 10px;
  }
}
