html {
  font-size: 16px;
  font-family: "droidSans", tahoma, sans-serif;
  font-weight: 400;
  color: #000000;
  box-sizing: border-box;
  height: 100vh; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a[href] {
  text-decoration: none;
  transition: 0.3s; }
  a[href]:hover {
    color: #474747; }
  a[href]:active {
    opacity: 0.5; }

img {
  max-width: 100%;
  min-height: 100%; }

.container {
  margin: 0 auto;
  max-width: 940px; }

.header {
  background: rgba(30, 9, 6, 0.15);
  height: 110px; }
  .header ul {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0; }

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%; }

.header__nav {
  margin-right: 44px; }
  .header__nav li:not(:last-of-type) {
    margin-right: 40px; }
  .header__nav a {
    font-size: 14px;
    color: #000000;
    letter-spacing: 0.08em; }
    .header__nav a:hover {
      border-bottom: 1px solid #000000; }

.header__list-icon li:not(:last-of-type) {
  margin-right: 34px; }

.header__list-icon li:first-of-type svg {
  fill: none; }

.header__list-icon li:last-of-type {
  margin-right: 13px; }
  .header__list-icon li:last-of-type a {
    display: block;
    position: relative;
    padding-right: 7px; }
    .header__list-icon li:last-of-type a svg {
      transition: 0.3s; }
    .header__list-icon li:last-of-type a:active {
      opacity: 1; }
      .header__list-icon li:last-of-type a:active svg {
        opacity: 0.5; }

.header__list-icon span {
  position: absolute;
  top: 0;
  right: 0;
  color: #f20505;
  font-size: 10px; }

.catalog {
  padding: 200px 0 50px 0;
  position: relative;
  background: linear-gradient(transparent 25px, #3f3f3f 25px, #3f3f3f 29px, transparent 30px), linear-gradient(transparent 40px, #3f3f3f 40px, #3f3f3f 44px, transparent 45px), linear-gradient(transparent 55px, #3f3f3f 55px, #3f3f3f 59px, transparent 60px), linear-gradient(transparent 70px, #3f3f3f 70px, #3f3f3f 74px, transparent 75px), linear-gradient(transparent 85px, #3f3f3f 85px, #3f3f3f 89px, transparent 90px), linear-gradient(transparent 100px, #3f3f3f 100px, #3f3f3f 104px, transparent 104px), linear-gradient(transparent 92%, #3f3f3f 92.1%, #3f3f3f 93%, transparent 93.1%), no-repeat left 201% url("../img/bg-guitar.png"); }
  .catalog:after {
    display: block;
    position: absolute;
    bottom: 60%;
    right: 0;
    width: 50%;
    height: 300px;
    transform: rotate(180deg);
    content: '';
    background: no-repeat url("../img/bg-guitar.png"); }
  .catalog h1 {
    font-weight: 700;
    font-size: 22px;
    margin: 0 0 20px 0; }

.catalog__breadcrumbs {
  display: flex;
  align-items: center;
  margin-bottom: 50px; }
  .catalog__breadcrumbs a {
    font-size: 16px;
    color: #474747;
    margin-right: 10px; }
  .catalog__breadcrumbs svg {
    margin-right: 10px; }

.filter h2 {
  font-weight: 700;
  font-size: 18px;
  margin: 0 0 30px 0; }

.filter__item {
  border-top: 1px solid #dddada;
  padding: 30px 0; }
  .filter__item h2 {
    margin: 0 0 24px 0; }

.filter__item-fields {
  display: flex;
  align-items: center; }
  .filter__item-fields div {
    width: 14px;
    height: 1px;
    background-color: #585757;
    margin: 0 13px; }
  .filter__item-fields input {
    width: 84px;
    height: 28px;
    border-radius: 2px;
    border: 1px solid #9f9e9e; }
    .filter__item-fields input::placeholder {
      font-size: 14px;
      color: rgba(0, 0, 0, 0.6);
      text-align: center; }

.filter__item-check label {
  display: block;
  margin-bottom: 10px; }
  .filter__item-check label:hover::before {
    border-color: #585757; }
  .filter__item-check label:active::before {
    border-color: #c1c1c1; }
  .filter__item-check label::before {
    display: inline-block;
    vertical-align: middle;
    width: 24px;
    height: 24px;
    content: '';
    border: 1px solid #9f9e9e;
    border-radius: 2px;
    margin-right: 5px; }

.filter__item-check input[type="checkbox"] {
  display: none; }

.filter__item-check input:checked + label::before {
  background: no-repeat center url("../img/mark.png"); }

.footer {
  min-height: 273px;
  background: #3f3f3f;
  color: #ffffff; }
  .footer ul {
    list-style: none;
    padding: 0;
    margin: 0; }

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 60px; }

.footer__logo {
  margin-bottom: 53px; }

.footer__socials ul {
  display: flex; }

.footer__socials li:not(:last-of-type) {
  margin-right: 23px; }

.footer__info {
  display: flex;
  justify-content: flex-end; }

.footer__item {
  width: 17%;
  margin-left: 22px; }
  .footer__item:first-of-type {
    margin-left: 0; }
  .footer__item:last-of-type {
    margin-left: 97px; }
  .footer__item h2 {
    margin: 22px 0 15px 0;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 0.12em; }
  .footer__item p {
    margin: 0 0 12px 0;
    font-size: 10px;
    line-height: 12px; }
    .footer__item p:last-of-type {
      margin-bottom: 0; }
  .footer__item a {
    color: #ffffff;
    font-size: 10px;
    letter-spacing: 0.13em; }
    .footer__item a[href^="tel"] {
      display: block;
      margin-bottom: 10px; }
  .footer__item li {
    line-height: 50%; }
  .footer__item li:not(:last-of-type) {
    margin-bottom: 10px; }
  .footer__item address {
    font-style: normal;
    font-size: 10px; }
